import request from '@/utils/request'
// 查询店铺支付配置信息
export function findWechatPay(id,t) {
  return request({
    url: `/sys/wechatPay/findWechatPay?shopId=${id}&payType=${t}`,
    method: 'get',
  })
}
// 添加微信支付配置信息
export function addWechatPayInfo(data) {
  return request({
    url: `/sys/wechatPay/addWechatPayInfo`,
    method: 'post',
    data
  })
}
// 添加微信支付配置信息
export function editWechatPayInfo(data) {
  return request({
    url: `/sys/wechatPay/editWechatPayInfo`,
    method: 'post',
    data
  })
}

// 查询店铺支付配置信息
export function setPayInvalid(id,t,pid) {
  return request({
    url: `/sys/wechatPay/setPayInvalid?shopId=${id}&payType=${t}&payId=${pid}`,
    method: 'get',
  })
}