import request from '@/utils/request'
// 获取扫码地址
export function getShopPayType(id) {
  return request({
    url: `/sys/wechatPay/getShopPayType?shopId=${id}`,
    method: 'get',
  })
}
// 设置店铺收款类型
export function setShopPayType(id,t) {
  return request({
    url: `/sys/wechatPay/setShopPayType?shopId=${id}&payType=${t}`,
    method: 'get',
  })
}
// 设置店铺收款类型
export function findShopMpWechat(id,type) {
  return request({
    url: `/sys/businessWechat/findShopMpWechat?shopId=${id}&wechatType=${type}`,
    method: 'get',
  })
}