<template>
  <div class="pageContent">
    <el-card shadow="always">
      <div class="item flex-row-start">
        <div class="name">当前配置支付能力的渠道：</div>
        <div class="info">{{form.nickName}}</div>
      </div>
    </el-card>
    <el-card shadow="always" class="box" style="margin-top:20px;">
      <div class="flex-row-start flex-wrap">
        <div class="item flex-row-start">
          <div class="name">渠道类型：</div>
          <div class="info">{{payType == 1 ? '微信公众号' : '微信小程序'}}</div>
        </div>
        <div class="item flex-row-start">
          <div class="name">渠道授权主体：</div>
          <div class="info">{{form.principalName}}</div>
        </div>
        <div class="item flex-row-start">
          <div class="name">APPID：</div>
          <div class="info">{{form.authorizerAppid}}</div>
        </div>
        <div class="item flex-row-start">
          <div class="name">渠道昵称：</div>
          <div class="info">{{form.nickName}}</div>
        </div>
        <div class="item flex-row-start">
          <div class="name">微信认证状态：</div>
          <el-tag type="danger" v-if="form.verifyTypeInfo == -1">未认证</el-tag>
          <el-tag type="success" v-if="payType == 1 &&form.verifyTypeInfo == 0">微信认证</el-tag>
          <el-tag type="danger" v-if="payType == 2 &&form.verifyTypeInfo == 0">未认证</el-tag>
          <el-tag type="success" v-if="payType == 1 && form.verifyTypeInfo == 1">新浪微博认证</el-tag>
          <el-tag type="success" v-if="payType == 2 && form.verifyTypeInfo == 1">已认证</el-tag>
          <el-tag type="success" v-if="form.verifyTypeInfo == 2">腾讯微博认证</el-tag>
          <el-tag type="warning" v-if="form.verifyTypeInfo == 3">已资质认证通过但还未通过名称认证</el-tag>
          <el-tag type="warning" v-if="form.verifyTypeInfo == 4">已资质认证通过、还未通过名称认证，但通过了新浪微博认证</el-tag>
          <el-tag type="success" v-if="form.verifyTypeInfo == 5">已资质认证通过、还未通过名称认证，但通过了腾讯微博认证</el-tag>
        </div>
      </div>
    </el-card>
    <el-card shadow="always" class="box" style="margin-top:20px;">
      <el-table :data="list" style="width: 100%">
        <el-table-column label="支付方式">
          <template slot-scope="scope" :data="scope.row.id">微信支付</template>
        </el-table-column>
        <el-table-column label="模式">
          <template slot-scope="scope">
            <div class="flex-row-center iBox" :data="scope">
              <span>自有模式</span>
              <el-tooltip class="item" effect="dark" content="商户使用自己在微信支付官网自主申请的微信公众号支付,资金由微信支付结算,手续费率由微信支付官方决定。" placement="top">
                <i class="el-icon-question item"></i>
              </el-tooltip>
            </div>
          </template>
        </el-table-column>
        <el-table-column label="启停状态">
          <template slot-scope="scope">
            <div style="color:#F56C6C;" v-if="scope.row.invalid == 0">未启用</div>
            <div style="color:#67C23A;" v-if="scope.row.invalid == 1">启用</div>
          </template>
        </el-table-column>
        <el-table-column label="配置指引">
          <template slot-scope="scope">
            <el-button type="text" size="small" :data="scope">查看指引</el-button>
          </template>
        </el-table-column>
        <el-table-column label="操作">
          <template slot-scope="scope">
            <el-button type="text" size="small" v-if="scope.row.invalid == 0" @click="openConfig(scope.row)">开启</el-button>
            <el-button type="text" size="small" @click="configShowFun(scope.row)">配置</el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-card>
    <!-- 支付配置弹出框 -->
    <el-dialog title="支付配置" :visible.sync="configShow" width="50%">
      <el-form :model="configForm" ref="configForm" label-width="120px" class="demo-ruleForm">
        <el-form-item label="配置渠道：">
          <div>{{payType == 1 ? '微信公众号' : '微信小程序'}}</div>
        </el-form-item>
        <el-form-item label="支付产品：">
          <div>微信支付</div>
        </el-form-item>
        <el-form-item label="支付模式：">
          <div>自有模式</div>
        </el-form-item>
        <el-form-item label="微信商户名称：" required>
          <el-input style="width:400px;" v-model.trim="configForm.mchName" maxlength="30" show-word-limit></el-input>
        </el-form-item>
        <el-form-item label="微信商户号：" required>
          <el-input style="width:400px;" v-model.trim="configForm.mchId" maxlength="15" show-word-limit></el-input>
        </el-form-item>
        <el-form-item label="APPID：">
          <div>{{configForm.appId}}</div>
        </el-form-item>
        <el-form-item label="秘钥：" required>
          <el-input style="width:400px;" v-model.trim="configForm.mchKey" maxlength="32" show-word-limit></el-input>
        </el-form-item>
        <el-form-item label="证书：" required>
          <el-upload
            class="upload-demo"
            action="/sys/wechatPay/uploadCertificatePath"
            name="file"
            :on-success="p12Success"
            :on-remove="handleRemove"
            :before-upload="beforeAvatarUpload"
            :limit="1"
            :file-list="fileList">
            <el-button size="small" type="primary">点击上传</el-button>
            <div slot="tip" class="el-upload__tip">只能上传p12文件</div>
          </el-upload>
        </el-form-item>
        <el-form-item>
          <el-button :loading="subLoading" type="primary" @click="submitForm('ruleForm')">编 辑</el-button>
          <el-button @click="configShow = false">关 闭</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>
<script>
import { mapState } from 'vuex'
import { findWechatPay , addWechatPayInfo ,setPayInvalid ,editWechatPayInfo } from '@/api/moneyConfig/configList'
import { findShopMpWechat } from '@/api/moneyConfig/payConfig'
export default {
  data(){
    return{
      form:{},
      list:[],
      configShow:false,               // 配置弹框
      configForm:{},                  // 配置的数据
      fileList:[],
      configType:'new',
      subLoading:false,
    }
  },
  created(){
    this.payType = this.$route.query.payType
    this.configForm.payType = this.payType
    this.getData()
    this.getTopData()
  },
  methods:{
    // 获取下部数据
    getData(){
      findWechatPay(this.shop.shopId,this.payType).then(res=>{
        console.log('获取配置',res)
        if(res.code == 200){
          if(res.data.length == 0){
            this.configType = 'new'
            let obj = {};
            obj.invalid = 0
            obj.payType = this.payType
            this.list.push(obj)
          }else{
            this.configType = 'edit'
            this.list = res.data
          }
        }
      })
    },
    // 获取上部数据
    getTopData(){
      findShopMpWechat(this.shop.shopId,this.payType).then(res=>{
        console.log('获取公众号数据',res)
        this.form = res.data
      })
    },
    // 开启
    openConfig(e){
      if(!e.id){
        this.$message.error('微信支付自有模式还未配置完整。请配置完整后再进行开启。');
        return;
      }
      setPayInvalid(this.shop.shopId,this.payType,e.id).then(res=>{
        console.log('开启',res)
        if(res.code == 200){
          this.$message({
            message: '开启成功',
            type: 'success'
          });
          this.getData()
        }else{
          this.$message.error(res.message);
        }
      })

    },
    // 配置 - 弹出框
    configShowFun(e){
      console.log('e',e)
      if(this.configType == 'edit'){
        this.configForm = this.list[0]
        let obj = {
          name:this.list[0].certificateName,
          url:this.list[0].certificatePath
        }
        this.fileList.push(obj)
      }
      console.log('打开弹框configType',this.configType)
      this.configForm.appId = this.form.authorizerAppid
      this.configShow = true;
    },
    // 上传证书之前验证
    beforeAvatarUpload(file) {
      console.log('上传之前',file) 
      const isP12 = file.type === 'application/x-pkcs12';
      if (!isP12) {
        this.$message.error('上传文件只能是 p12 格式!');
      }
      return isP12;
    },
    // 保存配置
    submitForm(){
      console.log('configForm',this.configForm)
      if(!this.configForm.mchName){
        this.$message.error('请填写微信商户名称');
        return;
      }
      if(!this.configForm.mchId){
        this.$message.error('请填写微信商户号');
        return;
      }
      if(!this.configForm.mchKey){
        this.$message.error('请填写秘钥');
        return;
      }
      if(!this.configForm.certificatePath){
        this.$message.error('请上传证书');
        return;
      }
      this.configForm.shopId = this.shop.shopId
      this.configForm.mchType = 1
      this.subLoading = true;
      console.log('成功了',this.configForm,'this.configType',this.configType)
      if(this.configType == 'new'){
        addWechatPayInfo(this.configForm).then(res=>{
          console.log('配置',res)
          if(res.code == 200){
            this.getData()
            this.configShow = false;
            this.$message({
              message: '配置成功',
              type: 'success'
            });
            setTimeout(()=>{
              this.subLoading = false
            },1000)
          }else{
            this.$message.error(res.message);
            this.subLoading = false
          }
        })
      }
      if(this.configType == 'edit'){
        editWechatPayInfo(this.configForm).then(res=>{
          console.log('配置',res)
          if(res.code == 200){
            this.getData()
            this.configShow = false;
            this.$message({
              message: '配置成功',
              type: 'success'
            });
            setTimeout(()=>{
              this.subLoading = false
            },1000)
          }else{
            this.$message.error(res.message);
            this.subLoading = false
          }
        })
      }
      
    },
    p12Success(file, fileList){
      console.log('file',file,'fileList',fileList)
      this.configForm.certificatePath = file.data.name
      this.configForm.certificateName = fileList.name
    },
    handleRemove(file, fileList) {
      this.configForm.certificatePath = ''
      this.configForm.certificateName = ''
    },
  },
  computed: {
    ...mapState([
      'user',
      'shop'
    ])
  },

}
</script>
<style lang="less" scoped>
.item{
  margin-right: 80px;
  line-height: 60px;
}
.name{
  color: #303133;
}
.info{
  color: #909399;
}
.iBox /deep/ .item{
  margin-right: 0;
}
</style>